"use client";

import React, { useEffect, useState } from "react";

const WorldMap = () => {
  const [tooltip, setTooltip] = useState({ show: false, text: "", x: 0, y: 0 });

  useEffect(() => {
    // Load and inject the SVG
    fetch("/world-map.svg")
      .then((response) => response.text())
      .then((svgContent) => {
        const mapContainer = document.getElementById("map-container");
        if (mapContainer) {
          mapContainer.innerHTML = svgContent;

          // Style all paths and add hover events
          const allPaths = mapContainer.querySelectorAll("path");
          allPaths.forEach((path) => {
            path.style.fill = "#ECEFF1";
            path.style.stroke = "#607D8B";
            path.style.strokeWidth = "0.5";
            path.style.transition = "fill 0.2s";

            // Add hover events
            path.addEventListener("mousemove", (e) => {
              const countryName = getCountryName(path.id);
              setTooltip({
                show: true,
                text: countryName,
                x: e.clientX,
                y: e.clientY,
              });
              path.style.fill = "#D0D9DC"; // Hover color
            });

            path.addEventListener("mouseleave", () => {
              setTooltip({ show: false, text: "", x: 0, y: 0 });
              path.style.fill = "#ECEFF1"; // Reset color
            });
          });
        }
      });
  }, []);

  // Function to convert country code to name
  const getCountryName = (code: string) => {
    const countryNames: { [key: string]: string } = {
      us: "United States",
      ca: "Canada",
      gb: "United Kingdom",
      fr: "France",
      de: "Germany",
      // Add more countries as needed
      // You can add the full list from the mapping you provided
    };
    return countryNames[code] || code.toUpperCase();
  };

  return (
    <div className="relative">
      <div
        id="map-container"
        className="w-full h-screen flex items-center justify-center"
      />
      {tooltip.show && (
        <div
          className="absolute z-10 bg-black text-white px-2 py-1 rounded text-sm pointer-events-none"
          style={{
            left: `${tooltip.x + 10}px`,
            top: `${tooltip.y - 20}px`,
            transform: "translate(-50%, -100%)",
          }}
        >
          {tooltip.text}
        </div>
      )}
    </div>
  );
};

export default WorldMap;
